@import '../../../assets/scss/color';
@import '../../../assets/scss/fonts';





















































































































































































































































































































































































































































































































































































@mixin overlay {
  opacity: 0.4;
  pointer-events: none;
  border-radius: 10px;
}
:focus {
  outline: 0;
}
.el-drawer__body {
  overflow: auto;
}
.circulationDetails {
  .bjs-powered-by {
    display: none !important;
  }
  .highlight-overlay-had {
    background-color: #44b365;
    @include overlay;
  }
  .highlight-overlay-ing {
    background-color: #eaa926;
    @include overlay;
  }
  .highlight-overlay-chaos {
    background-image: linear-gradient(to bottom right, #eaa926, #44b365);
    @include overlay;
  }
  .highlight-overlay-reject {
    background-color: #d43636;
    @include overlay;
  }
  .djs-overlay {
    cursor: pointer;
  }
}
