@import '../../assets/scss/color';
@import '../../assets/scss/fonts';


























































































































































































































































































































































































































.historyAssignment {
  .operate-left-class {
    background-color: $color-back-header;
    transition: background-color 0.8s;
    border: 1px solid #ebedf2;
    padding: 20px 20px 0;
    .radioClass {
      margin-bottom: 20px;
    }
    .el-form {
      .tagsClass {
        display: flex;
        .el-form-item__content {
          flex: 1;
        }
        .el-tag {
          min-width: 62px;
          text-align: center;
          border: 1px solid $color-control;
          margin-right: 20px;
          background-color: $color-back-header;
          transition: background-color 0.8s;
          font-size: 14px;
          color: $color-main-text;
          cursor: pointer;
        }
        .selectedTag {
          color: $color-main;
          background-color: $color-back-header;
          border: 1px solid $color-main;
        }
      }
    }
  }
  .tag {
    margin: 0 5px;
  }
  .tag-normal {
    border-color: $color-Success;
    color: $color-Success;
    background-color: rgba(103, 194, 58, 0.08);
  }
  .tag-timeout {
    border-color: $color-Error;
    color: $color-Error;
    background-color: rgba(245, 108, 108, 0.08);
  }
  .tag-will {
    border-color: $color-Warning;
    color: $color-Warning;
    background-color: rgba(255, 183, 27, 0.08);
  }
  .icon {
    margin-left: 2px;
  }
  .actionList {
    // position: absolute;
    float: right;
    top: 0;
    right: 0px;
    z-index: 1002;
    .el-button {
      display: block;
      margin: 4px 0;
    }
  }
  .dialogClass {
    .el-dialog__footer {
      text-align: center;
    }
  }
  .histroyClass {
    .el-dialog__footer {
      text-align: center;
      .el-button:last-child {
        display: none;
      }
    }
    .el-dialog__body {
      max-height: 500px;
      overflow-y: auto;
    }
  }
}
